body {
    margin: 0 !important;
    padding: 0 !important;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    /* background-color: black; */
}

.d-flex {
    display: flex;
}

#menu-items {
    justify-content: center;
    margin: 0;
    padding: 0;
}

#menu-items li {
    list-style: none;
    margin: 0 3rem;
    display: flex;
    align-items: center;
    height: 50px;
}

#menu-items li a:link {
    color: black;
    text-decoration: none;
}

#menu-items li a:hover {
    color: orange;
}

h1 {
    display: flex;
    justify-content: center;
}

.div-container {
    display: flex;
    height: 350px;
    justify-content: center;
}

.div-img {
    width: 20%;
    background-repeat: no-repeat;
    background-position-x: center;
    background-size: cover;
}

.portada-p {
    width: 80%;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
}